.banner_home {
  background-image: url(../images/banner-bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  padding-top: 144px;
  padding-bottom: 95px;
  .banner_home-title {
    font-size: 52px;
    line-height: 1.26;
    font-weight: 300;
    color: $gray;
    text-align: center;
  }
  .banner_home-text {
    font-size: 22px;
    line-height: 1.68;
    color: $gray;
    text-align: center;
    margin-top: 16px;
  }
  .banner_home-content > .btn {
    position: relative;
    background-color: $green;
    border: none;
    margin: 27px auto 0 auto;
    &::before {
      position: absolute;
      top: calc(50% - 9px);
      left: 27px;
      display: block;
      width: 9px;
      height: 18px;
      content: '';
      background-image: url('../images/arrow_btn_icon.svg');
      background-repeat: no-repeat;
    }
  }
}
