.services {
  padding-top: 30px;
  padding-bottom: 31px;
  .services-list {
    display: flex;
    flex-wrap: wrap;
    gap: 18px;
    .services-item {
      text-align: center;
      & > .services-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 64px;
        height: 64px;
        border-radius: 50%;
        background-color: $green;
        margin: 0 auto;
      }
      & > .services-title {
        margin-top: 9px;
      }
      & > .services-text {
        margin-top: 15px;
      }
    }
  }
}
