.team {
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
  .content-text {
    margin-bottom: 37px;
  }
}

.team-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  .team-item {
    & > .team-name {
      font-size: 23px;
      line-height: 1.2;
      margin-top: 15px;
    }
    & > .team-position {
      margin-top: 11px;
    }
  }
}
