$screen-xs: 320px;
$screen-sm: 480px;
$screen-md: 768px;
$screen-lg: 1360px;

@mixin respond($media) {
  @if $media == $screen-xs {
    @media only screen and (min-width: $screen-xs) {
      @content;
    }
  } @else if $media == sm {
    @media only screen and (min-width: $screen-sm) {
      @content;
    }
  } @else if $media == md {
    @media only screen and (min-width: $screen-md) {
      @content;
    }
  } @else if $media == lg {
    @media only screen and (min-width: $screen-lg) {
      @content;
    }
  }
}
