.callback {
  background-color: $gray-dark;
  & > .container {
    padding-top: 26px;
    padding-bottom: 30px;
  }
  .callback-form {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    & > .input-text {
      background-color: $white_color;
      border: 1px solid $input-border;
    }
  }
}
