.about_us {
  background-color: $green;
  .about_us-content {
    padding-top: 30px;
    padding-bottom: 30px;
    color: $white_color;
    & > .container {
      @include respond(sm) {
        max-width: calc(100% - 60px);
      }
    }
    .content-text {
      margin-bottom: 14px;
    }
  }
}
