.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 21px;
  padding-bottom: 21px;
  & > .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 20px;
    padding-right: 20px;
  }
  .header_nav {
    margin-top: 10px;
  }
}

.header_nav-list {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  .header_nav-item {
    & > .header_nav-link {
      color: $white_color;
    }
  }
}
